import React, { Component, Fragment } from 'react';
import { Route, Switch } from 'react-router-dom';

import './App.css';
import Welcome from './components/welcome/Welcome';

export default class App extends Component {
  state = {
    Core: null,
  };

  componentWillMount() {
    import('./components/core/Core').then(Core => {
      this.setState({ Core: Core.default });
    });
  }

  render() {
    let { Core } = this.state;

    if (!Core) {
      return <div>loading....</div>;
    } else {
      return (
        <div className="app">
          <Switch>
            <Route exact path="/" component={Welcome} />
            <Route path="/its" component={Core} />
            <Route
              path="/tram"
              render={props => <Core {...props} section="TRAM" />}
            />
            <Route
              path="/bus"
              render={props => <Core {...props} section="BUS" />}
            />
            <Route
              path="/train"
              render={props => <Core {...props} section="TRAIN" />}
            />
            <Route component={Welcome} />
          </Switch>
        </div>
      );
    }
  }
}



